.ingredientTitle {
    composes: text text_type_main-medium from '~@ya.praktikum/react-developer-burger-ui-components/dist/ui/common.css';
    height: 48px;
}

.container {
    composes: ml-15 mr-15 from '~@ya.praktikum/react-developer-burger-ui-components/dist/ui/box.css';
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(100% - 70px);

}

.infoLine {
    composes: mb-15 from '~@ya.praktikum/react-developer-burger-ui-components/dist/ui/box.css';
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.infoLineItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}